// Navbar Brand - Use this class when dealing the app includes the brand logos
// -------------------------

.navbar-branded {
    min-height: 70px; //Min height is needed to keep the height consistent when the link nav isn't there
}
.navbar-branded .nav {
	padding-top: 12px;
	font-size: 1.2em;
    
}

.navbar-branded .navbar-search {
	padding-top: 15px;
}

.navbar-branded a.navbar-brand>span {
	padding-left: 10px;
	padding-right: 20px;
	vertical-align: middle;
	font-size: 1.4em;
}

.navbar-branded .navbar-brand {
	padding: 5px 5px;
}
.navbar-branded .nav-collapse {
	padding-top: 10px;
}


//Add Padding to the Brand Logo
.navbar-branded .navbar-brand>img {
	padding-right: 20px;
	
}

//Adjusting the padding for Branded Nav text to make-up for the offset. 
.navbar-branded .navbar-text {
	margin-top: 12px;
}
    
