// Data Visualization and CSS Styles for
// charting, canvas, or related elements
// --------------------------------

.chart-red {
	color: @dv-red;
}

.chart-slate-green {
	color: @dv-slate-green;
}

.chart-dk-green {
	color: @dv-dark-green;
}

.chart-dk-orange {
	color: @dv-dark-orange;
}

.chart-med-purple {
	color: @dv-med-purple;
}

.chart-purple {
	color: @dv-purple;
}

.chart-orange {
	color: @dv-gold;
}

.chart-blue {
	color: @dv-blue;
}

.chart-green {
	color: @dv-green;
}

.chart-turquoise {
	color: @dv-turquoise;
}

.dv-processing-bkg, .dv-content-bkg {
	background-color: @dv-blue;
}

.dv-redirect-bkg {
	background-color: @dv-turquoise;
}

.dv-request-bkg {
	background-color: @dv-purple;
}

.dv-ssl-bkg {
	background-color: @dv-med-purple;
}

.dv-response-bkg {
	background-color: @dv-green;
}

.dv-http-200-bkg {
	background-color: @dv-dark-green;
}

.dv-dns-bkg {
	background-color: @dv-slate-green;
}

.dv-complete-bkg {
	background-color: @dv-red;
}

.dv-connect-bkg {
	background-color: @dv-dark-orange;
}

.dv-http-300-bkg {
	background-color: @dv-gold;
}

.dv-processing, .dv-content {
	color: @dv-blue;
}

.dv-redirect {
	color: @dv-turquoise;
}

.dv-request {
