// Icon Library for Branded Bootstrap
//
// We've taken and expanded on the base library used in Bootstrap (Glyphicons)
// and added our own package for icons and fonts. This package has been compiled
// from several different packages, but keeps the same basic catch for it. To ease 
// the transition from the existing libraries to the new one, we're adding aliases to
// comparable icons.

// Import the fonts

@import "icon-variables.less";


@font-face {
  font-family: 'icomoon-font-edited';
  src: ~"url('@{icon-font-path}@{icon-font-name}.eot')";
  src: ~"url('@{icon-font-path}@{icon-font-name}.eot?#iefix') format('embedded-opentype')",
       ~"url('@{icon-font-path}@{icon-font-name}.woff') format('woff')",
       ~"url('@{icon-font-path}@{icon-font-name}.ttf') format('truetype')",
       ~"url('@{icon-font-path}@{icon-font-name}.svg#bb-icons') format('svg')";
}

//Base class for styling an icon
.icon, .glyphicon {
	font-family: 'icomoon-font-edited';
  	position: relative;
  	top: 1px;
  	display: inline-block;
  	font-style: normal;
  	font-weight: normal;
  	line-height: 1;
  
  	speak: none;
	font-variant: normal;
	text-transform: none;

	// Better Font Rendering
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

