/*
 * Branded Stylings for data-tables plugin
 */
 
.data-table {
	margin: 0 auto;
	clear: both;
	width: 100%;
}

.data-table td {
	padding: 3px 10px;
}

.data-table .center,
.data-table .data-tables-empty {
	text-align: center;
}

.data-table tr:nth-child(odd) { 
	background-color: @blue-lighter; 
}
.data-table tr:nth-child(even) { 
	background-color: @white; 
}

.data-table tr:nth-child(odd) .sorting-1, 
.data-table tr:nth-child(odd) .sorting-2,  
.data-table tr:nth-child(odd) .sorting-3 { 
	background-color: @gray-light; 
}

.data-table tr:nth-child(even) .sorting-1,
.data-table tr:nth-child(even) .sorting-2,
.data-table tr:nth-child(even) .sorting-3 { 
	background-color: @gray-light; 
}

/*
 * Table wrapper
 */
.data-tables-wrapper {
	position: relative;
	clear: both;
}


/*
 * Page length menu
 */
.data-tables_length {
	float: left;
}


/*
 * Filter
 */
.data-tables-filter {
	float: right;
	text-align: right;
}


/*
 * Table information
 */
.data-tables-info {
	clear: both;
	float: left;
}


/*
 * Pagination
 */
.data-tables-paginate {
	float: right;
	text-align: right;
}

/* Two button pagination - previous / next */
.paginate-disabled-previous,
.paginate-enabled-previous,
.paginate-disabled-next,
.paginate-enabled-next {
	height: 19px;
	float: left;
	cursor: pointer;
	color: @link-color !important;
}
.paginate-disabled-previous:hover,
.paginate-enabled-previous:hover,
.paginate-disabled-next:hover,
.paginate-enabled-next:hover {
	text-decoration: none !important;
}
.paginate-disabled-previous:active,
.paginate-enabled-previous:active,
.paginate-disabled-next:active,
.paginate-enabled-next:active {
	outline: none;
}

.paginate-disabled-previous,
.paginate-disabled-next {
	color: @gray !important;
	cursor: auto;
}
.paginate-disabled-previous,
.paginate-enabled-previous {
	padding-left: 23px;
}
.paginate-disabled-next,
.paginate-enabled-next {
	padding-right: 23px;
	margin-left: 10px;
}

.paginate-enabled-previous:before {  
	font-family: FontAwesome;
	font-style: normal;
	color: @link-color;
	content: "\e6b5"; 
	padding-right: 10px;
}
/* .paginate_enabled_previous { font-family: Helvetica, Arial, sans-serif;} */
.paginate-enabled-previous:hover { color: #333; }
.paginate-disabled-previous:before { 
	content: "\e6b5"; 
	color: @gray-light; 
	padding-right:10px; 
	font-family: "icomoon-font-edited";
	font-style: normal;
}

.paginate-enabled-next:after { 
	font-family: "icomoon-font-edited";
	font-style: normal;
	color: @link-color;
	content: "\e6b6"; 
	padding-left: 10px; 
}
/* .paginate_enabled_next{ font-family: Helvetica, Arial, sans-serif;} */
.paginate-enabled-next:hover { color: #333; }
.paginate-disabled-next:after { 
	font-family: "icomoon-font-edited";
	font-style: normal;
	content: "\e6b6"; 
	color: @gray-light; 
}

/* Full number pagination */
.paging-full-numbers {
	height: 22px;
	line-height: 22px;
}
.paging-full-numbers a:active {
	outline: none
}
.paging-full-numbers a:hover {
	text-decoration: none;
}

.paging-full-numbers .paginate-button,
.paging-full-numbers a.paginate-active {
	border: 1px solid @gray-text-tertiary;
	-webkit-border-radius: 5px;
	-moz-border-radius: 5px;
	border-radius: 5px;
	padding: 2px 5px;
	margin: 0 3px;
	cursor: pointer;
	color: @gray-text !important;
}

.paging-full-numbers .paginate-button {
	background-color: @gray-light;
}

.paging-full-numbers a.paginate-button:hover {
	background-color: darken(@link-color, 10%);
	text-decoration: none !important;
}

.paging-full-numbers a.paginate-active {
	background-color: @gray;
}


/*
 * Processing indicator
 */
.data-tables-processing {
	position: absolute;
	top: 50%;
	left: 50%;
	width: 250px;
	height: 30px;
	margin-left: -125px;
	margin-top: -15px;
	padding: 14px 0 2px 0;
	border: 1px solid #eee;
	text-align: center;
	color: @gray-text-tertiary;
	font-size: 14px;
	background-color: @white;
}


/*
 * Sorting
 */
.sorting { 
	content: "\e7ce"; 
	color: @link-color; 
	padding-left:10px; 
	right: 0;
	font-family: "icomoon-font-edited";
	font-style: normal;
	text-align: right;
	float: right;
}


.sorting-asc:after { 
	content: "\e7d0"; 
	color: @link-color; 
	padding-left:10px; 
	right: 0;
	font-family: "icomoon-font-edited";
	font-style: normal;
	text-align: right;
	float: right;
}
.sorting_desc:after { 
	content: "\e7cf"; 
	color: @link-color; 
	padding-left:10px; 
	font-family: "icomoon-font-edited";
	font-style: normal;
	text-align: right;
	float: right;
}
.sorting_asc_disabled { color: @gray; }
.sorting_desc_disabled { color: @gray; }
 
table.data-table thead th:active,
table.data-table thead td:active {
	outline: none;
}


/*
 * Scrolling
 */
.data-tables-scroll {
	clear: both;
}

.data-tables-scroll-body {
	-webkit-overflow-scrolling: touch;
}
