.tabs-bottom, .tabs-right, .tabs-left {
  .nav-tabs {
    border-bottom: 0;
  }
}
 
.tab-content > .tab-pane,
.pill-content > .pill-pane {
  display: none;
}
 
.tab-content > .active,
.pill-content > .active {
  display: block;
}
 
.tabs-bottom {
  > .nav-tabs {
    border-top: 1px solid @gray;
 
    > li {
      margin-top: -1px;
      margin-bottom: 0;
 
      > a {
        -webkit-border-radius: 0 0 4px 4px;
           -moz-border-radius: 0 0 4px 4px;
                border-radius: 0 0 4px 4px;
 
        &:hover, &:focus {
          border-top-color: @gray;
          border-bottom-color: transparent;
        }
      }
    }
    > .active a {
      &, &:hover, &:focus {
        border-color: transparent @gray @gray @gray;
      }
    }
  }
}
 
.tabs-left, .tabs-right {
  > .nav-tabs > li {
    float: none;
 
    > a {
      min-width: 74px;
      margin-right: 0;
      margin-bottom: 3px;
    }
  }
}
 
.tabs-left > .nav-tabs {
  float: left;
  margin-right: 19px;
  border-right: 1px solid @gray;
 
  > li > a {
    margin-right: -1px;
    -webkit-border-radius: 4px 0 0 4px;
       -moz-border-radius: 4px 0 0 4px;
            border-radius: 4px 0 0 4px;
 
    &:hover, &:focus {
      border-color: @gray-lighter @gray @gray-lighter @gray-lighter;
    }
  }
 
  > .active a {
    &, &:hover, &:focus {
      border-color: @gray transparent @gray @gray;
//      *border-right-color: @white;
    }
  }
}
 
.tabs-right > .nav-tabs {
  float: right;
  margin-left: 19px;
  border-left: 1px solid @gray;
 
  > li > a {
    margin-left: -1px;
    -webkit-border-radius: 0 4px 4px 0;
       -moz-border-radius: 0 4px 4px 0;
            border-radius: 0 4px 4px 0;
 
    &:hover, &:focus {
      border-color: @gray-lighter @gray-lighter @gray-lighter @gray;
    }
  }
 
  > .active a {
    &, &:hover, &:focus {
      border-color: @gray @gray @gray transparent;
//      *border-left-color: @white;
    }
  }
}