// Footer styles for a specific pattern instead of the "fixed navbar" that currently exist

.footer {
    width: 100%;
    min-height: 30px;
    background-color: @brand-primary;
    padding-left: 20px;
    color: @white;
    font-size:.9em;
    vertical-align:middle;
}

.footer-fixed {
    position: absolute;
    bottom: 0;
}

.footer > div {
    display:inline-block;
    min-height:30px;
    padding-top:5px;
    vertical-align: middle;
}

.footer .nav {
    display:inline-block;
    margin:0 auto;
    vertical-align:middle;
    padding-left:40px;
}

.footer .nav > li {
    float:left;
    font-size:.9em;
    line-height:.9;
    margin: 0 auto;
}

.footer .nav > li > a {
    vertical-align:middle;
    color:@white;
}

.footer .nav > li > a:hover {
    background-color:transparent;
    text-decoration:underline;
}