//Additional styles for form validation to improve accessibility

//Add the background color for the errors
.has-error input.form-control {
	background-color: @state-danger-input-bg;
	&:focus {
		background-color: @state-danger-input-bg;
	}
}


// Validation add-on icon outside of the form
// -------------------------
.form-error-addon {
  padding: @padding-base-vertical @padding-base-horizontal;
  font-size: @font-size-base;
  font-weight: normal;
  line-height: 1;
  color: @error-color;
  text-align: center;
  //background-color: @input-group-addon-bg;
  //border: 1px solid @input-group-addon-border-color;
  border-radius: @border-radius-base;

 font-size: 2em;

  // Nuke default margins from checkboxes and radios to vertically center within.
  input[type="radio"],
  input[type="checkbox"] {
    margin-top: 0;
  }
}