//Pattern Build for inputs

/* Style Guide Pattern library: Input Validation
 * This is a style for inline style for validation 
 *
*/
.form-required:after {
	color: @error-color;
	font-weight: bold;
	//font-size: 1.1em;
	padding-left: 6px;
	font-family: 'icomoon-font-edited';
	content: @icon-asterisk;
}

//Removing label as a qualifier
.form-wait:after {

}

.form-success:after {

}

.has-error label:after {
	font-family: 'icomoon-font-edited';
	content: @icon-warning-sign;
	color: @error-color;
	font-weight: bold;
	//font-size: 1.4em;
	padding-left: 6px;
}

.has-warning label:after {
	font-family: 'icomoon-font-edited';
	content: @icon-exclamation;
	color: @brand-warning;
	font-weight: bold;
	//font-size: 1.1em;
	padding-left: 6px;
}

.has-success label:after {
	font-family: 'icomoon-font-edited';
	content: @icon-ok-solid;
	color: @brand-success;
	font-weight: bold;
	//font-size: 1.4em;
	padding-left: 6px;
}

.input-helper {
	color: @gray-text;
	background-color: @gray-lighter;
	margin-top: 2px;
	padding: 2px 5px 5px 2px;
}